import React, { useState, useEffect } from 'react';
import axios from 'axios';

const MapWithMarkers = ({ restaurants, selectedFoodType }) => {
  const [selectedRestaurant, setSelectedRestaurant] = useState(null);

  useEffect(() => {
    //     
    const script = document.createElement('script');
    script.src =
      'https://dapi.kakao.com/v2/maps/sdk.js?appkey=0560729fbda53d06035a69b6f812c484&libraries=services';
    script.async = true;
    script.onload = () => {
      //     
      const centerLat = 36.773536; //   
      const centerLng = 126.933500; //   

      //   
      const mapOptions = {
        center: new window.kakao.maps.LatLng(centerLat, centerLng), //   
        level: 2, //   
      };

      const map = new window.kakao.maps.Map(
        document.getElementById('map'),
        mapOptions
      );

      //    
      window.kakao.maps.event.addListener(map, 'click', function (mouseEvent) {
        //  ,   
        const latlng = mouseEvent.latLng;

        const message =
          latlng.getLat().toFixed(6) + ', ' + latlng.getLng().toFixed(6);

        const resultDiv = document.getElementById('result');
        resultDiv.innerHTML = message;
      });

      //      
      const filteredRestaurants = restaurants.filter(
        (restaurant) => restaurant.type === selectedFoodType
      );

      //   
      filteredRestaurants.forEach((restaurant) => {
        const marker = new window.kakao.maps.Marker({
          position: new window.kakao.maps.LatLng(
            restaurant.latitude,
            restaurant.longitude
          ),
          map: map,
        });

        //   
        window.kakao.maps.event.addListener(marker, 'click', () => {
          setSelectedRestaurant(restaurant);
        });

        //    
        window.kakao.maps.event.addListener(marker, 'mouseover', () => {
          //    
          setSelectedRestaurant(restaurant);
        });

        //    
        window.kakao.maps.event.addListener(marker, 'mouseout', () => {
          //    
          setSelectedRestaurant(null);
        });
      });
    };

    //  `<head>`  
    document.head.appendChild(script);
  }, [restaurants, selectedFoodType]);

  return (
    <div>
      <div id="map" style={{ width: '100%', height: '500px', margin: '0 auto' }}></div>
      <div id="result"></div>
      {selectedRestaurant && (
        <div className="restaurant-info">
          <h3>{selectedRestaurant.name}</h3>
          <p> : {selectedRestaurant.type}</p>
          <p> : {selectedRestaurant.openingHours}</p>
          <p>: {selectedRestaurant.address}</p>
        </div>
      )}
    </div>
  );
};

function MainPage() {
  const [selectedFoodType, setSelectedFoodType] = useState(null);
  const [restaurants, setRestaurants] = useState([]);

  useEffect(() => {
    //      
    const fetchRestaurantData = async () => {
      try {
        const response = await axios.get('https://api.example.com/restaurants'); //   API 
        setRestaurants(response.data);
      } catch (error) {
        console.error('Error fetching restaurant data:', error);
      }
    };

    fetchRestaurantData();
  }, []);

  const foodTypes = ['', '', '', '', '', '', ''];

  return (
    <div className="App">
      <div className="food-type-buttons">
        {foodTypes.map((foodType, index) => (
          <button
            key={foodType}
            onClick={() => setSelectedFoodType(foodType)}
            className={selectedFoodType === foodType ? 'active' : ''}
            style={{
              backgroundColor: selectedFoodType === foodType ? 'grey' : '#ff6480',
              color: 'white',
              border: '0px solid black',
              padding: '5px 10px',
              borderRadius: '5px',
              marginTop: '5px',
              marginRight: '10px',
              marginBottom: '5px',
              cursor: 'pointer',
              marginLeft: index === 0 ? '10px' : '0',
            }}
          >
            {foodType}
          </button>
        ))}
      </div>
      <MapWithMarkers restaurants={restaurants} selectedFoodType={selectedFoodType} />
    </div>
  );
}

export default MainPage;
