import React from 'react';
import { Link } from 'react-router-dom';

function SmallMenu({ name, userid }) {
  return (
    <li>
      {name === '' && <Link to="/myinfo"></Link>}
      {/* {name === '' && <Link to={`/myinfo?userid=${userid}`}></Link>} */}
      {/* {name === '' && <Link to="/myinfo?userid=cdflavor1113"></Link>} */}
      {name === ' ' && <Link to="/myinfoheart"> </Link>}
      {/* {name === '  ' && <Link to="/">  </Link>} */}
      
      {name === '' && <Link to="/mapfoodtype"></Link>}
      {name === '' && <Link to="/mapfoodarea"></Link>}
      {/* {name === '' && <Link to="/recomkeyword"></Link>} */}
      {name === '' && <Link to="/weather"></Link>}

      {name === ' ' && <Link to="/boardlist"> </Link>}
      {name === ' ' && <Link to="/suggestlist"> </Link>}
      {name === ' ' && <Link to="/vote"> </Link>}

      {name === ' ' && <Link to="/foodworldcup"> </Link>}
      {name === '' && <Link to="/recomrandom"></Link>}

      {name === '' && <Link to="/setting"></Link>}
    </li>
  );
}

export default SmallMenu;